/* 01. Google Font */

/* 02. General Settings */

html,
body {
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    -moz-font-smoothing: antialiased;
    font-smoothing: antialiased;
}

body {
    font-family: 'Rambla', sans-serif;
    color: #888;
    font-weight: 400;
    font-size: 15px;
    line-height: 1.75em;
    min-height: 100%;
    position: relative;
    overflow-x: hidden;
    background-color: #191919;
}

p {
    font-family: 'Rambla', sans-serif;
    font-size: 15px;
    line-height: 1.75em;
    font-weight: 400;
    color: #888;
    margin: 0 0 20px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    color: #fff;
    font-weight: 500;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
    font-weight: inherit;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a,
h1 a:hover,
h2 a:hover,
h3 a:hover,
h4 a:hover,
h5 a:hover,
h6 a:hover,
h1 a:focus,
h2 a:focus,
h3 a:focus,
h4 a:focus,
h5 a:focus,
h6 a:focus {
    color: inherit;
    text-decoration: none;
}

hr {
    background-color: #eee;
    border: 0;
    height: 3px;
    margin-top: 60px;
    margin-bottom: 60px;
}

ul,
ol,
dl,
table,
pre,
form {
    margin: 0;
}

ul {
    list-style-type: disc;
    padding: 0;
}

ol {
    list-style-type: decimal;
    padding: 0;
}

ul ul,
ol ul {
    list-style-type: circle;
}

ul ol,
ol ol {
    list-style-type: lower-alpha;
}

ol ol ol {
    list-style-type: decimal;
}

ul ul,
ol ul {
    padding-left: 1em;
}

ul ol,
ol ol {
    padding-left: 1.4em;
}

dt {
    font-weight: 500;
}

dd {
    margin-left: 0;
    padding-left: 1.5em;
}

figure {
    margin: 0;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
    border-width: 0;
    width: 100%;
}

caption {
    font-size: 2rem;
    text-align: left;
}

th {
    font-weight: 500;
}

td {
    font-weight: 400;
}

th,
td {
    border-bottom: 1px solid #f9f9f9;
    padding: .75em 0;
    text-align: left;
}

thead {
    font-size: 1.2rem;
    text-transform: uppercase;
}

thead th {
    padding: .5em 0;
    border-bottom-width: 3px;
}

table tfoot tr:last-child td {
    border-bottom: 0;
}

fieldset {
    border-width: 0;
    clear: both;
    margin: 0 0 3.25em;
    padding: 0;
}

fieldset > :last-child {
    margin-bottom: 0;
}

legend {
    border-bottom: 1px solid #f9f9f9;
    font-size: 1.3rem;
    font-weight: 500;
    line-height: 1.625;
    margin-bottom: 1.625em;
    padding-bottom: 0.53846154em;
    text-transform: uppercase;
    width: 100%;
}

dfn,
cite,
em,
i,
q {
    font-style: italic;
}

address {
    margin: 0 0 1.625em;
}

pre {
    font-size: .9rem;
    line-height: 1.6;
    max-width: 100%;
    overflow: auto;
    padding: 1.6em;
    white-space: pre;
    white-space: pre-wrap;
    word-wrap: break-word;
    background: #f9f9f9;
    border: 3px solid #f9f9f9;
    color: #101010;
}

code,
kbd,
tt,
var,
samp,
pre {
    font-family: 'Rambla', sans-serif;
    -webkit-hyphens: none;
    hyphens: none;
    -ms-hyphens: none;
}

ins {
    padding: 0 .3em;
    text-decoration: none;
    background-color: #fffad4;
    border-radius: 3px;
}

mark {
    padding: 0 .3em;
    background-color: #101010;
    color: #fff;
    border-radius: 3px;
}

abbr[title],
acronym[title] {
    text-decoration: none;
    border-bottom: 1px dotted #EEE;
    cursor: help;
}

img {
    border: 0;
    height: auto;
    -ms-interpolation-mode: bicubic;
    max-width: 100%;
    vertical-align: middle;
}

del,
.low-opacity {
    opacity: .6;
}

big,
.big {
    font-size: 120%;
}

small,
.small {
    font-size: 80%;
}

video {
    height: auto;
}

video,
embed,
iframe,
object {
    max-width: 100%;
    margin-bottom: 1.5em;
}

.mt-30 {
    margin-top: 30px;
}

.mb-20 {
    margin-bottom: 20px;
}


/* Links */

a,
a:hover {
    color: #F68338;
}

a {
    text-decoration: none;
    font-weight: 500;
}

a:focus {
    outline: inherit;
}

b,
strong {
    font-weight: bold;
    color: #fff;
}


/* Form elements */
/* text field */

button,
input,
optgroup,
select,
textarea {
    font-family: 'Rambla', sans-serif;
}

input[type="password"]:focus,
input[type="email"]:focus,
input[type="text"]:focus,
input[type="file"]:focus,
input[type="radio"]:focus,
input[type="checkbox"]:focus,
textarea:focus {
    outline: none;
}

input[type="password"],
input[type="email"],
input[type="text"],
input[type="file"],
textarea {
    max-width: 100%;
    margin-bottom: 15px;
    border-color: #222;
    padding: 10px 15px;
    height: auto;
    background-color: #222;
    box-shadow: none;
    border: none;
    display: block;
    width: 100%;
    font-size: 15px;
    line-height: 1.5em;
    font-weight: 400;
    color: #888;
    background-image: none;
}






select {
    padding: 10px;
    border-radius: 5px;
}

table,
th,
tr,
td {
    border: 1px solid #101010;
}

th,
tr,
td {
    padding: 10px;
}

input[type="radio"],
input[type="checkbox"] {
    display: inline;
}



/* Placeholder */

::-webkit-input-placeholder {
    color: #888;
    font-size: 15px;
    font-weight: 400;
}

:-moz-placeholder {
    color: #888;
}

::-moz-placeholder {
    color: #888;
    opacity: 1;
}

:-ms-input-placeholder {
    color: #888;
}


/* button */
.btn {
    padding: 8px 20px;
    cursor: pointer;
    background: #F68338;
    -webkit-appearance: none;
    position: relative;
    color: #fff;
    font-size: 15px;
    font-weight: 400;
    display: inline-block;
    border-radius: 0;
    border: 1px solid #F68338;
}

.btn:hover {
    color: #fff;
    background-color: #222;
    border: 1px solid #222;
}


/* 03. Useful classes */

.sr-only,
.screen-reader-text {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0px, 0px, 0px, 0px);
    border: 0 none;
}

.clearfix:after {
    content: "";
    display: table;
    clear: both;
}

.clear {
    clear: both;
    display: block;
    height: 0;
    overflow: hidden;
    visibility: hidden;
    width: 0;
}

.alignleft {
    float: left;
    margin-right: 1.65em;
    margin-bottom: 1.65rem;
}

.alignright {
    float: right;
    margin-left: 1.65em;
    margin-bottom: 1.65rem;
}

.aligncenter {
    clear: both;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.text-left {
    text-align: left;
}

.text-center {
    text-align: center;
}

.text-right {
    text-align: right;
}

.text-uppercase {
    text-transform: uppercase;
}

.no-bottom {
    color: #101010;
    font-weight: 500;

    word-spacing: 0;
    margin-bottom: 0px;
    font-size: 15px;
    text-transform: uppercase
}

.d-none {
    display: none;
}

.grid-container {
    display: -ms-flexbox;
    display: -webkit-box;
    display: inline-block;
    flex-wrap: wrap;
    margin-right: -30px;
    margin-left: -30px;
}

.grid-container .grid-column {
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 30px;
    padding-left: 30px;
}

@media (min-width: 768px) and (max-width: 991px),
(min-width: 1200px) {
    .grid-container .grid-column {
        flex-basis: 0;
        flex-grow: 1;
        max-width: 100%;
    }

    .grid-container .order-first {
        order: -1;
    }
}

@media (min-width: 1440px) {
    .grid-container {
        margin-right: -60px;
        margin-left: -60px;
    }

    .grid-container .grid-column {
        padding-right: 60px;
        padding-left: 60px;
    }
}


/* Themify */

.ti:before {
    display: inline-block;
    font: normal normal normal 24px/1 "Themify";
    font-size: inherit;
    text-rendering: auto;
    line-height: inherit;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.ti-chevron-right:before {
    content: "\e649";
}

.ti-chevron-left:before {
    content: "\e64a";
}

.ti-plus:before {
    content: "\e61a";
}


/* List */



.list {
    margin-bottom: 0px;
}

.list li {
    display: block;
    text-align: left;
    position: relative;
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 15px;
}

.list li strong {
    font-size: 15px;
    color: #fff;
}

.list li:last-child {
    margin-bottom: 0px;
}

/* Instagram Thumb */

.insta-thumb {
    float: left;
    width: 100%;
}

.insta-thumb a {
    float: left;
    width: 33.3%;
    padding: 5px;
    overflow: hidden;
    transition: all 0.3s ease-in-out;
}

.insta-thumb a img {
    width: 100%;
    height: auto;
    border-radius: 4px;
    margin: 0;
}

.insta-thumb a:hover {
    opacity: 0.6;
}

.divider1 {
    height: 1px;
    width: 100%;
    background: #222;
    margin: 60px 0;
}

.divider2 {
    height: 1px;
    width: 100%;
    background: #222;
    margin: 60px 0;
}


/* 04. Owl Carousel */

.owl-carousel {
    display: none;
    width: 100%;
    -webkit-tap-highlight-color: transparent;
    position: relative;
    z-index: 1;
}

.owl-carousel .owl-stage {
    position: relative;
    touch-action: manipulation;
    -moz-backface-visibility: hidden;
}

.owl-carousel .owl-stage:after {
    content: ".";
    display: block;
    clear: both;
    visibility: hidden;
    line-height: 0;
    height: 0;
}

.owl-carousel .owl-stage-outer {
    position: relative;
    overflow: hidden;
    -webkit-transform: translate3d(0px, 0px, 0px);
}

.owl-carousel .owl-wrapper,
.owl-carousel .owl-item {
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -ms-backface-visibility: hidden;
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
}

.owl-carousel .owl-item {
    position: relative;
    min-height: 1px;
    float: left;
    -webkit-backface-visibility: hidden;
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
}

.owl-carousel .owl-item img {
    display: block;
    width: 100%;
}

.owl-carousel .owl-nav.disabled,
.owl-carousel .owl-dots.disabled {
    display: none;
}

.owl-carousel .owl-nav .owl-prev,
.owl-carousel .owl-nav .owl-next,
.owl-carousel .owl-dot {
    cursor: pointer;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.owl-carousel .owl-nav button.owl-prev,
.owl-carousel .owl-nav button.owl-next,
.owl-carousel button.owl-dot {
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
}

.owl-carousel.owl-loaded {
    display: block;
}

.owl-carousel.owl-loading {
    opacity: 0;
    display: block;
}

.owl-carousel.owl-hidden {
    opacity: 0;
}

.owl-carousel.owl-refresh .owl-item {
    visibility: hidden;
}

.owl-carousel.owl-drag .owl-item {
    touch-action: pan-y;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.owl-carousel.owl-grab {
    cursor: move;
    cursor: -webkit-grab;
    cursor: grab;
}

.owl-carousel.owl-rtl {
    direction: rtl;
}

.owl-carousel.owl-rtl .owl-item {
    float: right;
}

.no-js .owl-carousel {
    display: block;
}

.owl-carousel .animated {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

.owl-carousel .owl-animated-in {
    z-index: 0;
}

.owl-carousel .owl-animated-out {
    z-index: 1;
}

.owl-carousel .fadeOut {
    -webkit-animation-name: owlFadeOut;
    animation-name: owlFadeOut;
}

@-webkit-keyframes owlFadeOut {
    0% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

@keyframes owlFadeOut {
    0% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

.owl-height {
    transition: height .2s ease-in-out;
}

.owl-carousel .owl-item .owl-lazy {
    opacity: 0;
    transition: opacity .5s ease;
}

.owl-carousel .owl-item .owl-lazy[src^=""],
.owl-carousel .owl-item .owl-lazy:not([src]) {
    max-height: 0;
}

.owl-carousel .owl-item img.owl-lazy {
    transform-style: preserve-3d;
}

.owl-carousel .owl-video-wrapper {
    position: relative;
    height: 100%;
}

.owl-carousel .owl-video-play-icon {
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    width: 90px;
    height: 90px;
    line-height: 90px;
    text-align: center;
    margin: -60px 0 0 -60px;
    background-color: #EEE;
    border-radius: 50%;
    cursor: pointer;
    -webkit-backface-visibility: hidden;
    transition: background .3s;
}

.owl-carousel .owl-video-playing .owl-video-tn,
.owl-carousel .owl-video-playing .owl-video-play-icon {
    display: none;
}

.owl-carousel .owl-video-tn {
    opacity: 0;
    height: 100%;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;
    transition: opacity .3s ease;
}

.owl-carousel .owl-video-frame {
    position: relative;
    z-index: 1;
    height: 100%;
    width: 100%;
}

@media (min-width: 768px) {
    .hero-media .owl-carousel .owl-video-play-icon {
        width: 100px;
        height: 100px;
        line-height: 100px;
        margin: -60px 0 0 -60px;
    }
}

.owl-carousel .owl-video-play-icon:hover {
    background-color: #101010;
}

.owl-carousel .owl-video-play-icon:before {
    content: '\F40A';
    font: normal normal normal 48px/1 "Themify";
    text-rendering: auto;
    line-height: inherit;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}


