/* 16. Timeline styles */

.timeline {
    list-style: none;
    padding: 0;
    margin: 0;
    position: relative;
    padding-top: 15px;
    padding-bottom: 15px;
}

.timeline:before {
    content: "";
    position: absolute;
    width: 1px;
    left: 8px;
    top: 10px;
    bottom: 10px;
    background-color: #222;
}

.timeline.boxed-timeline:before {
    top: 0;
    bottom: 0;
}

.timeline > li {
    margin-bottom: 15px;
    margin-left: 10px;
}

.timeline > li:last-child {
    margin-bottom: 0;
}

.timeline > li:hover .timeline-content:before {
    background-color: #F68338;
    border: 1px solid #F68338;
}

.timeline > li .timeline-content {
    padding: 0 50px;
    position: relative;
}

.timeline > li .timeline-content:before {
    content: "";
    position: absolute;
    left: 0;
    top: 3px;
    width: 14px;
    height: 14px;
    background-color: #222;
    border: 1px solid #222;
    transition: all .3s ease;
    z-index: 100;
    margin-left: -8px;
    border-radius: 50%;
}

.timeline > li .timeline-content h4 {
    margin-top: 0px;
    font-weight: 700;
    color: #fff;
    font-size: 18px;
    font-family: 'Rambla', sans-serif;
}

.timeline > li .timeline-content h5 {
    display: block;
    margin: 0 0 5px 0;
    font-size: 12px;
    text-transform: uppercase;
    color: #888;
    letter-spacing: 1px;
    font-weight: 400;
}

.timeline > li .timeline-content p {
    margin-top: 5px;
    margin-bottom: 30px;
}

.timeline > li .timeline-content .timeline-date {
    position: relative;
    display: inline-block;
    font-size: 13px;
    font-weight: 400;
    text-transform: uppercase;
    color: #888;
    transition: all .3s ease;
}


/* 17. Project styles */

.project {
    position: relative;
    margin-bottom: 30px;
}

.project .desc {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    right: 0;
    background: rgba(246, 131, 56, 0.8);
    opacity: 0;
    transition: .3s
}

.project .desc .con {
    padding: 30px;
    display: block;
    position: relative;
}

.project .desc .con .icon {
    position: absolute;
    bottom: 30px;
    left: 30px;
    right: 30px
}

.project .desc .con .icon span {
    display: inline;
    padding-right: 5px
}

.project .desc .con .icon span a {
    color: #ccc;
    padding: 5px;
    background: #101010;
    border-radius: 4px
}

.project .desc .con .icon i {
    font-size: 17px;
    color: #ccc
}

.project .desc h3 {
    color: #fff;
    font-size: 18px;
    font-weight: 700;
    transition: transform .3s, opacity .3s;
    transform: translate3d(0, -15px, 0);
    margin-bottom: 5px;
}

.project .desc h3 a {
    color: #fff
}

.project .desc span {
    display: block;
    color: #fff;
    font-size: 15px;
    font-weight: 400;
    letter-spacing: 0px;
    transition: transform .3s, opacity .3s;
    transform: translate3d(0, 15px, 0)
}

.project .desc span a {
    color: #101010
}

.project:hover .desc {
    opacity: 1
}

.project:hover .desc h3 {
    transform: translate3d(0, 0, 0)
}

.project:hover .desc span {
    transform: translate3d(0, 0, 0)
}

.project-desc h2 {
    color: #101010;
